import React from "react";
//Bootstrap
import {ResponsiveEmbed,
        Container
} from 'react-bootstrap';
const TableroCovid = () => {

    let embedLoad = () => {
        console.log("Loaded");
    }


    
    return (
        <React.Fragment>
        <div className="embed-container">
            <iframe onChange={() => {return embedLoad}} scrolling="no" src="https://covidgtoc.herokuapp.com/" frameborder="0" allow="picture-in-picture"></iframe>    
        </div>
        </React.Fragment>

)}

export default TableroCovid;